import React from 'react';
import { Link } from 'gatsby';

const LanguageItemView = ({langKey, isSelected, onClick, label}) => (
  <button
    onClick={() => onClick(langKey)}
    style={{backgroundColor:isSelected ? 'green':'gray', border: 'none'}}
  >
    {label}
  </button>
);

/**
 * Language Choice View
 * 
 * @param {function} t i18n function
 */
const CompLangChoice = ({
  currentLangKey,
  availableLangs,
  handleChangeLanguage,
}) => (
  <>
    {availableLangs.map(lang => (
      <LanguageItemView 
        key={lang.key}
        langKey={lang.key}
        isSelected={currentLangKey===lang.key}
        label={lang.label} 
        onClick={handleChangeLanguage}
      />
    ))}
  </>
);

export default CompLangChoice;
