import React, { Component } from "react"
import classNames from "classnames"
import { withTranslation } from "react-i18next"

import CompLangChoiceView from '../views/comp-lang-choice';

class LanguageSwitcher extends Component {

  constructor(props) {
    super(props)
    const { i18n } = this.props
    this.state = { language: i18n.language }
    this.handleChangeLanguage = this.handleChangeLanguage.bind(this)
  }

  static getDerivedStateFromProps(props, state) {
    if (props.i18n.language === state.language) {
      return null;
    }
    return {
      language: props.i18n.language
    }
  }

  handleChangeLanguage(lng) {
    const { i18n } = this.props
    i18n.changeLanguage(lng);
  }

  render() {
    const { languages: {availableLangs} } = this.props;
    return (
      <CompLangChoiceView
        currentLangKey={this.state.language}
        availableLangs={availableLangs}
        handleChangeLanguage={this.handleChangeLanguage}
      />
    )
  }
}
export default withTranslation("LanguageSwitcher")(LanguageSwitcher)
