
import { getUserStore } from '../../stores';

export const getUser = () => getUserStore().getUser();

export const isLoggedIn = () => getUserStore().isLoggedIn();

export const login = async ({ email, password }: { email: string; password: string }) => (
  await getUserStore().signin({ identifier: email, password })
);

export const signup = async ({ 
  username, email, password 
}: { username: string; email: string; password: string }) => (
  await getUserStore().register({ username, email, password, provider: 'local' })
);

export const logout = () => getUserStore().signout();

export const forgetPassword = async (email: string, resetPasswordUrl: string) => (
  await getUserStore().getApi().auth_forgot_password(email, resetPasswordUrl)
);

export const resetPassword = async (code: string, password: string, passwordConfirmation: string) => (
  await getUserStore().getApi().auth_reset_password(code, password, passwordConfirmation)
);

export const changePassword = async (oldPassword: string, newPassword: string, newPasswordConfirmation: string) => (
  await getUserStore().getApi().auth_change_password(oldPassword, newPassword, newPasswordConfirmation)
);
