import React from 'react';
//import { Link } from 'gatsby';
import TLink from '../../elements/TransitionLink';
/**
 * NavBar component view
 * 
 * @param {object} menuItems menu items
 */
const CompNavBar = ({
  menuItems,
}) => (
  <nav>
  <ul>
    {menuItems.map(item => (
      <li key={item.name}>
        {!item.subItems && 
          <h3>
            {item.link
              ? <TLink to={item.link}>
                  {item.name}
                </TLink>
              : <div>{item.name}</div>
            }
          </h3>
        }
        {item.subItems && 
          <div><h3>
            {item.link
              ? <TLink to={item.link}>
                  {item.name}
                </TLink>
              : <div>{item.name}</div>
            }
          </h3>
          <ul>
            {item.subItems.map((subitem) => (
              <li key={subitem.name}><h4>
                {subitem.link
                  ? <TLink to={subitem.link}>
                      {subitem.name}
                    </TLink>
                  : <div>{subitem.name}</div>
                }
              </h4></li>
            ))}
          </ul>
          </div>
        }
      </li>
    ))}
  </ul>
  </nav>
);

export default CompNavBar;
