/**
 * Page Transition Link
 */

import React from 'react';
//@ts-ignore
import AniLink from 'gatsby-plugin-transition-link/AniLink';

interface TLinkProps {
  to: string;
}

const TLink: React.FunctionComponent<TLinkProps> = ({
  to,
  children
}) => {
  // paintDrip, swipe, cover, or fade
  return (
    <AniLink fade to={to}>
      {children}
    </AniLink>
  )
}

export default TLink;
