import React, { FunctionComponent } from "react"
import { useStaticQuery, graphql } from "gatsby"

import CompLayoutHeaderView from '../../views/comp-layout/comp-layout-header';
import CompLayoutFooterView from '../../views/comp-layout/comp-layout-footer';

import './default.css'

const query = graphql`
query {
  site {
    siteMetadata {
      title,
      languages {
        defaultLangKey,
        availableLangs {
          key, label
        }
      }
    }
  }
}`;

const DefaultLayout: FunctionComponent<{ children: React.ReactNode }> = ({
  children
}) => {
  const { site } = useStaticQuery(query);

  return (
  <>
    <CompLayoutHeaderView siteTitle={site.siteMetadata.title} />
    <main>{children}</main>
    <CompLayoutFooterView languages={site.siteMetadata.languages}/>
  </>
  );
}
export default DefaultLayout;
