import React, { FunctionComponent } from "react";
import { isLoggedIn } from "../../services/auth";
import {
  loggedInMenuItems,
  nonLoggedInMenuItems
} from '../../constants/menus';

import CompNavBar from "../../views/comp-layout/comp-nav-bar";

export const NavBar: FunctionComponent = (props) => {
  const menuItems = isLoggedIn() ? loggedInMenuItems : nonLoggedInMenuItems;

  return (
    <CompNavBar
      menuItems={menuItems}
    />
  );
}
